<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }" @click.native="clearStorage">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        style="margin-bottom: 10px"
        title="注意：只能给商品添加参数"
        type="warning"
        :closable="true"
        show-icon>
      </el-alert>
      <!-- 选择商品区域 -->
      <el-row class="selectRow">
        <el-col>
          <span>请选择商品：</span>
          <el-cascader
            style="width: 300px"
            v-model="selectValue"
            :options="cateList"
            :props="carProps"
            @change="handleChange">
          </el-cascader>
        </el-col>
      </el-row>
      <!-- 页签区域 -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" :disabled="isShowBtn" @click="addVisible = true">添加参数</el-button>
          <el-table
            :data="manyList"
            border
            style="width: 100%"
          >
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  :key="index"
                  v-for="(tag, index) in scope.row.attr_vals"
                  closable
                  @close="handleClose(scope.row, index)">
                  {{tag}}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="openEditParams(scope.row)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteParams(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" :disabled="isShowBtn" @click="addVisible = true">添加属性</el-button>
          <el-table
            :data="onlyList"
            border
            style="width: 100%"
          >
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  :key="index"
                  v-for="(tag, index) in scope.row.attr_vals"
                  closable
                  @close="handleClose(scope.row, index)">
                  {{tag}}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label="属性名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="openEditParams(scope.row)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteParams(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!-- 添加参数与属性的对话框 -->
      <el-dialog
        :title="textTitle"
        :visible.sync="addVisible"
        width="50%"
        @close="resetAddForm"
      >
        <el-form label-width="80px" :model="addForm" ref="addFormRefs" :rules="addFormRules">
          <el-form-item :label="labelText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addVisible = false">取 消</el-button>
          <el-button type="primary" @click="addParams">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改的对话框 -->
      <el-dialog
        :title="editTitle"
        :visible.sync="editVisible"
        width="50%"
        @close="resetEditForm"
        ref="editRefs"
      >
        <el-form label-width="80px" :model="editForm" ref="editFormRefs" :rules="addFormRules">
          <el-form-item :label="labelText" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editVisible = false">取 消</el-button>
          <el-button type="primary" @click="editParams">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      cateList: [],
      carProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      selectValue: [],
      activeName: 'many',
      manyList: [],
      onlyList: [],
      addVisible: false,
      editVisible: false,
      addForm: {
        attr_name: ''
      },
      editForm: {
        attr_name: '',
        attr_id: 0
      },
      addFormRules: {
        attr_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCateList()
  },
  computed: {
    isShowBtn () {
      if (this.selectValue.length === 3) {
        return false
      }
      return true
    },
    selectGoodsId () {
      return this.selectValue[this.selectValue.length - 1]
    },
    textTitle () {
      if (this.activeName === 'many') {
        return '添加参数'
      } else {
        return '添加属性'
      }
    },
    editTitle () {
      if (this.activeName === 'many') {
        return '修改参数'
      } else {
        return '修改属性'
      }
    },
    labelText () {
      if (this.activeName === 'many') {
        return '参数名称'
      } else {
        return '属性名称'
      }
    }
  },
  methods: {
    clearStorage () {
      window.sessionStorage.setItem('activePath', '')
      this.$router.go(0)
    },
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')
      this.cateList = res.data
    },
    handleChange () {
      this.getParamsData()
    },
    async getParamsData () {
      const { data: res } = await this.$http.get(`categories/${this.selectGoodsId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        this.manyList = res.data
      } else {
        this.onlyList = res.data
      }
    },
    handleClick () {
      this.getParamsData()
    },
    resetAddForm () {
      this.$refs.addFormRefs.resetFields()
    },
    resetEditForm () {
      this.$refs.editFormRefs.resetFields()
    },
    async addParams () {
      const { data: res } = await this.$http.post(`categories/${this.selectGoodsId}/attributes`, {
        attr_name: this.addForm.attr_name,
        attr_sel: this.activeName
      })
      if (res.meta.status !== 201) return this.$message.error('创建失败')
      this.$message.success('创建成功')
      this.getParamsData()
      this.addVisible = false
    },
    async editParams () {
      this.$refs.editRefs.validate(async (valid) => {
        if (!valid) return this.$message.error('提交信息有误')
        const { data: res } = await this.$http.put(`categories/${this.selectGoodsId}/attributes/${this.editForm.attr_id}`, {
          attr_name: this.editForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 200) return this.$message.error('创建失败')
        this.$message.success('创建成功')
        this.getParamsData()
        this.editVisible = false
      })
    },
    openEditParams (row) {
      this.editVisible = true
      this.editForm.attr_name = row.attr_name
      this.editForm.attr_id = row.attr_id
    },
    deleteParams (id) {
      this.$confirm('此操作将永久删除该信息, 是否继续?', '删除', {
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        type: 'error'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`categories/${this.selectGoodsId}/attributes/${id}`)
        if (res.meta.status !== 200) return this.$message.error('删除失败')
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getParamsData()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    async getValsInfo (row) {
      const { data: res } = await this.$http.put(`categories/${this.selectGoodsId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) return this.$message.error('失败')
      this.$message.success('成功')
    },
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      row.attr_vals.push(row.inputValue)
      row.inputValue = ''
      row.inputVisible = false
      this.getValsInfo(row)
    },
    handleClose (row, index) {
      row.attr_vals.splice(index, 1)
      this.getValsInfo(row)
    },
    showInput (row) {
      row.inputVisible = true
      // 页面上元素重新被渲染之后执行
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  }
}
</script>

<style lang="less" scope>
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.el-scrollbar__wrap{
  overflow-x: hidden;
}
</style>
